.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;

  .avatar {
    width: 120px;
    height: 120px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 20%;
    }
  }

  .menu {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    .title {
      font-size: 2.5rem;
    }

    .linkPages {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      font-size: 1.1rem;
      gap: 20px;
      margin-top: 20px;

      :global(.active) {
        color: #f8fafc;
      }

      a {
        text-decoration: none;
        color: #94a3b8;
        border-radius: 10px;

        &:hover {
          color: #f8fafc;
        }
      }
    }
  }
}

@media (min-width: 530px) {
  .header {
    justify-content: flex-start;

    .menu {
      align-items: flex-start;

      .linkPages {
        margin-top: 0;
        justify-content: flex-start;
      }
    }
  }
}